const categories = [
  { value: "all", label: "Tous" },
  { value: "produits-cheveux", label: "Produits Cheveux" },
  { value: "accessoires", label: "Accessoires" },
  { value: "produits-peau", label: "Soins Peau" },
  { value: "enfants", label: "Enfants" },
  { value: "hommes", label: "Hommes" },
]

const sortOptions = [
  { value: "featured", label: "Recommandés" },
  { value: "price-asc", label: "Prix croissant" },
  { value: "price-desc", label: "Prix décroissant" },
  { value: "name", label: "Nom A-Z" },
]

const heroSlides = [
  {
    id: 1,
    image: "/hero-salon.jpg",
    title: "Nouvelle Collection",
    subtitle: "Produits Bio & Naturels",
  },
  {
    id: 2,
    image: "/categories/femmes.jpg",
    title: "Soins Cheveux Naturels",
    subtitle: "Pour tous types de boucles",
  },
  {
    id: 3,
    image: "/services/box-braids.jpg",
    title: "Réservez Votre Service",
    subtitle: "Coiffure professionnelle",
  },
  {
    id: 4,
    image: "/products/shea-butter.jpg",
    title: "Bestsellers",
    subtitle: "Les produits les plus vendus",
  },
]

export default function HomePage() {


  const { user, isAuthenticated, isLoading } = useAuthSafe();
  const router = useRouter();

  console.log("Auth State:", { user, isAuthenticated, isLoading });


  const [searchQuery, setSearchQuery] = useState("")
  const [selectedCategory, setSelectedCategory] = useState("all")
  const [sortBy, setSortBy] = useState("featured")
  const [currentSlide, setCurrentSlide] = useState(0)

  //   // Redirection protégée uniquement après hydratation
  // useEffect(() => {
  //   if (!isLoading && !isAuthenticated) {
  //     router.push("/auth/login");
  //   }
  // }, [isLoading, isAuthenticated, router]);


  useEffect(() => {
    const timer = setInterval(() => {
      setCurrentSlide((prev) => (prev + 1) % heroSlides.length)
    }, 4000)
    return () => clearInterval(timer)
  }, [])

  const nextSlide = () => {
    setCurrentSlide((prev) => (prev + 1) % heroSlides.length)
  }

  const prevSlide = () => {
    setCurrentSlide((prev) => (prev - 1 + heroSlides.length) % heroSlides.length)
  }

  const filteredProducts = useMemo(() => {
    let filtered = products

    if (selectedCategory !== "all") {
      filtered = filtered.filter((p) => p.category === selectedCategory)
    }

    if (searchQuery) {
      filtered = filtered.filter((p) => p.name.toLowerCase().includes(searchQuery.toLowerCase()))
    }

    const sorted = [...filtered]
    switch (sortBy) {
      case "price-asc":
        sorted.sort((a, b) => a.price - b.price)
        break
      case "price-desc":
        sorted.sort((a, b) => b.price - a.price)
        break
      case "name":
        sorted.sort((a, b) => a.name.localeCompare(b.name))
        break
    }

    return sorted
  }, [selectedCategory, searchQuery, sortBy])

  // Pendant le chargement → on affiche rien ou un loader
  if (isLoading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-gold"></div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-background">
      <Header />
     
      <main className="pb-20 md:pb-8">
        <section className="relative bg-background overflow-hidden">
          <div className="relative h-48 md:h-64 lg:h-72">
            {/* Slides */}
            {heroSlides.map((slide, index) => (
              <div
                key={slide.id}
                className={`absolute inset-0 transition-opacity duration-500 ${
                  index === currentSlide ? "opacity-100" : "opacity-0"
                }`}
              >
                <Image
                  src={slide.image || "/placeholder.svg"}
                  alt={slide.title}
                  fill
                  className="object-cover brightness-75"
                  priority={index === 0}
                  sizes="100vw"
                />

                {/* Overlay Text */}
                <div className="absolute inset-0 flex items-center justify-center">
                  <div className="text-center px-4">
                    <h2 className="text-2xl md:text-4xl lg:text-5xl font-bold text-white mb-2">{slide.title}</h2>
                    <p className="text-sm md:text-lg text-white/90">{slide.subtitle}</p>
                  </div>
                </div>
              </div>
            ))}

            {/* Navigation Buttons */}
            <button
              onClick={prevSlide}
              className="absolute left-2 md:left-4 top-1/2 -translate-y-1/2 w-8 h-8 md:w-10 md:h-10 rounded-full bg-white/80 hover:bg-white flex items-center justify-center transition-colors z-10"
              aria-label="Previous slide"
            >
              <ChevronLeft className="w-5 h-5 md:w-6 md:h-6" />
            </button>

            <button
              onClick={nextSlide}
              className="absolute right-2 md:right-4 top-1/2 -translate-y-1/2 w-8 h-8 md:w-10 md:h-10 rounded-full bg-white/80 hover:bg-white flex items-center justify-center transition-colors z-10"
              aria-label="Next slide"
            >
              <ChevronRight className="w-5 h-5 md:w-6 md:h-6" />
            </button>

            {/* Dots Indicator */}
            <div className="absolute bottom-4 left-1/2 -translate-x-1/2 flex gap-2 z-10">
              {heroSlides.map((_, index) => (
                <button
                  key={index}
                  onClick={() => setCurrentSlide(index)}
                  className={`w-2 h-2 rounded-full transition-all ${
                    index === currentSlide ? "bg-gold w-6" : "bg-white/60 hover:bg-white/80"
                  }`}
                  aria-label={`Go to slide ${index + 1}`}
                />
              ))}
            </div>
          </div>
        </section>

        {/* Products Section */}
        <div id="products" className="container mx-auto px-4 py-6 md:py-8">
          {/* Header Section */}
          <div className="text-center mb-8 md:mb-10">
            <h2 className="text-3xl md:text-4xl lg:text-5xl font-bold tracking-tight mb-3">Notre Boutique</h2>
            <div className="w-24 h-1 bg-gradient-to-r from-transparent via-gold to-transparent mx-auto mb-4" />
            <p className="text-sm md:text-base text-muted-foreground max-w-2xl mx-auto text-pretty">
              Des produits naturels soigneusement sélectionnés pour sublimer vos cheveux
            </p>
          </div>

          {/* Search Bar */}
          <div className="mb-6">
            <div className="relative">
              <Search className="absolute left-3 top-1/2 -translate-y-1/2 w-5 h-5 text-muted-foreground" />
              <Input
                type="text"
                placeholder="Rechercher un produit..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                className="pl-10 h-12 text-base"
              />
            </div>
          </div>

          {/* Category Filters */}
          <div className="flex gap-2 mb-6 overflow-x-auto pb-2 scrollbar-hide">
            {categories.map((cat) => (
              <button
                key={cat.value}
                onClick={() => setSelectedCategory(cat.value)}
                className={`px-4 py-2.5 rounded-full text-sm font-medium whitespace-nowrap transition-all ${
                  selectedCategory === cat.value
                    ? "bg-gold text-gold-foreground shadow-lg shadow-gold/20"
                    : "bg-muted text-foreground hover:bg-muted/80"
                }`}
              >
                {cat.label}
              </button>
            ))}
          </div>

          {/* Sort and Results Count */}
          <div className="flex items-center justify-between mb-6">
            <p className="text-sm text-muted-foreground">
              {filteredProducts.length} produit{filteredProducts.length > 1 ? "s" : ""}
            </p>

            <select
              value={sortBy}
              onChange={(e) => setSortBy(e.target.value)}
              className="text-sm border border-border rounded-md px-3 py-2 bg-background"
            >
              {sortOptions.map((option) => (
                <option key={option.value} value={option.value}>
                  {option.label}
                </option>
              ))}
            </select>
          </div>

          {/* Products Grid */}
          {filteredProducts.length > 0 ? (
            <div className="grid grid-cols-2 sm:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 gap-4 md:gap-6">
              {filteredProducts.map((product) => (
                <Card
                  key={product.id}
                  className="overflow-hidden border-border group cursor-pointer hover:shadow-lg hover:border-gold/50 transition-all"
                >
                  <Link href={`/shop/${product.id}`}>
                    {/* Image */}
                    <div className="relative aspect-square overflow-hidden bg-muted">
                      <Image
                        src={product.image || "/placeholder.svg"}
                        alt={product.name}
                        fill
                        className="object-cover group-hover:scale-105 transition-transform duration-300"
                        sizes="(max-width: 640px) 50vw, (max-width: 1024px) 33vw, 20vw"
                      />

                      {/* Tag Badge */}
                      {product.tag && (
                        <div className="absolute top-2 left-2 bg-gold text-gold-foreground text-xs px-2 py-1 rounded-md font-medium shadow-lg">
                          {product.tag}
                        </div>
                      )}

                      {/* Stock Badge */}
                      {!product.inStock && (
                        <div className="absolute inset-0 bg-black/60 flex items-center justify-center">
                          <span className="text-white font-semibold text-sm">Rupture de stock</span>
                        </div>
                      )}

                      {/* Wishlist Button */}
                      <button
                        className="absolute top-2 right-2 w-8 h-8 bg-white/90 rounded-full flex items-center justify-center opacity-0 group-hover:opacity-100 transition-opacity"
                        onClick={(e) => {
                          e.preventDefault()
                          e.stopPropagation()
                        }}
                      >
                        <Heart className="w-4 h-4" />
                      </button>
                    </div>

                    {/* Content */}
                    <div className="p-3 md:p-4">
                      <h3 className="font-medium text-sm md:text-base mb-2 line-clamp-2 leading-tight">
                        {product.name}
                      </h3>
                      <p className="text-gold font-bold text-base md:text-lg mb-3">{product.price}€</p>

                      <Button
                        size="sm"
                        className="w-full h-9 text-sm bg-gold hover:bg-gold-dark text-gold-foreground"
                        disabled={!product.inStock}
                        onClick={(e) => {
                          e.preventDefault()
                          e.stopPropagation()
                        }}
                      >
                        <ShoppingCart className="w-4 h-4 mr-1" />
                        Ajouter
                      </Button>
                    </div>
                  </Link>
                </Card>
              ))}
            </div>
          ) : (
            <div className="text-center py-16">
              <p className="text-muted-foreground text-lg mb-2">Aucun produit trouvé</p>
              <p className="text-sm text-muted-foreground">Essayez une autre recherche ou catégorie</p>
            </div>
          )}
        </div>
      </main>

      <MobileNav />
    </div>
  )
}
